import React, { Component } from "react";
import "./App.css";
import App from "./App";
import Menu from "./pages/menu";
import Home from "./pages/home";
import About from "./pages/about";
import Servicios from "./pages/servicios";
import Reservas from "./pages/reservas";
import Carrito from "./pages/carrrito";
import Confirm_cart from "./components/confirm_cart";
import Admin from "./components/admin";
import Confirma_Reservas from "./components/confirmacion_reservas";
import { HashRouter as Routers, Switch, Route } from "react-router-dom";

class Router extends Component {
  render() {
    return (
      <Routers>
        <App>
          <Switch>
            <Route exact path="/menu" component={Menu}></Route>
            <Route exact path="/home" component={Home}></Route>
            <Route exact path="/about" component={About}></Route>
            <Route exact path="/reserva" component={Reservas}></Route>
            <Route exact path="/carrito" component={Carrito}></Route>
            <Route exact path="/servicios" component={Servicios}></Route>
            <Route exact path="/confirm" component={Confirm_cart}></Route>
            <Route exact path="/admin" component={Admin}></Route>
            <Route
              exact
              path="/Confirmacion_Reservas"
              component={Confirma_Reservas}
            ></Route>
          </Switch>
        </App>
      </Routers>
    );
  }
}

export default Router;
